.nav {
  text-align: center;
}
.nav ul {
  list-style-type: none;
  padding: 0;
}
.nav ul li {
  display: inline-block;
}
.nav li:after {
  content: '|';
  font-size: 12px;
  color: #ccc;
  width: 20px;
  display: inline-block;
}
.nav li:last-of-type:after {
  content: '';
  width: 0;
}
.nav a {
  font-size: 13px;
  text-decoration: none;
  color: #6b6882;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

